.modal {
  &.open {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  width: 0;
  height: 0;
  overflow: hidden;

  &__inner {
    width: 60vw;
    height: auto;
    background-color: #fff;
    padding: 20px 0;
    box-sizing: border-box;
  }
}
