.comment-card {
  border: 1px solid red;
  padding: 5px;
  box-sizing: border-box;
  width: 700px;

  &__name {
    display: block;
    width: 500px;
    background-color: brown;
    margin: 0;
    padding: 0;
  }

  &__body {
    width: 600px;
    background-color: blue;
    display: block;
  }
}
